import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import Loader from 'react-loader-spinner';
import 'react-loader-spinner/dist/loader/css/react-spinner-loader.css';

const styles = () => {
  return {
    loaderContainer: {
      width: '100%',
      position: 'absolute',
      left: 0,
      textAlign: 'center'
    },
    swaggerSpinner: {
      margin: '10px'
    }
  };
};

const Spinner = ({ loading, children, classes }) => {
  return (
    (loading && (
      <div className={classes.loaderContainer}>
        <Loader
          className={classes.swaggerSpinner}
          type="TailSpin"
          color="#324157"
          height={50}
          width={50}
          timeout={60000000}
        />
      </div>
    )) ||
    children
  );
};

export default withStyles(styles)(Spinner);
