import BaseAPI from './BaseAPI';

export default class GitReposAPI extends BaseAPI {
  static addGitRepo(organizationId, { url, name, username, password }) {
    return this.execute('post', `/organizations/${organizationId}/gitRepos`, {
      data: {
        url,
        name,
        git_username: username,
        git_password: password
      }
    });
  }

  static commitModelToRepos(repoId, modelId) {
    return this.execute(
      'post',
      `/gitRepos/${modelId}/models/${repoId}/commits?includeSwagger=true`
    );
  }

  static getRepos(organizationId) {
    return this.execute('get', `/organizations/${organizationId}/gitRepos`);
  }

  static testGit(repoId) {
    return this.execute('post', `/gitRepos/${repoId}/commits`, {
      data: {}
    });
  }

  static deleteGit(repoId) {
    return this.execute('delete', `/gitRepos/${repoId}`);
  }

  static updateGit(repoId, repo) {
    return this.execute('put', `/gitRepos/${repoId}`, { data: { ...repo } });
  }
}
