import React, { useState } from 'react';
import { Button, Snackbar, SnackbarContent } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import ErrorIcon from '@material-ui/icons/Error';

const useStyles = makeStyles(() => ({
  snackbarText: {
    display: 'flex',
    alignItems: 'center'
  },
  snackbarIcon: {
    marginRight: '0.6rem'
  },
  snackbarSaveButton: {
    marginRight: '0.5rem',
    color: '#55A646',
    border: '1px solid #448238',
    '&:hover': {
      color: '#448238',
      border: '1px solid #448238'
    }
  },
  snackbarCancelButton: {
    color: '#EB4C2F',
    border: '1px solid #EB4C2F',
    '&:hover': {
      color: '#c54129',
      border: '1px solid #c54129'
    }
  }
}));

export default function PositionedSnackbar(props) {
  const classes = useStyles();
  const [state] = useState({
    vertical: 'top',
    horizontal: 'center'
  });
  const { showSnack, saveRules, dismissSaveRules } = props;
  const { vertical, horizontal } = state;

  return (
    <Snackbar
      anchorOrigin={{ vertical, horizontal }}
      key={`${vertical},${horizontal}`}
      open={showSnack}
    >
      <SnackbarContent
        message={
          <span className={classes.snackbarText}>
            <ErrorIcon className={classes.snackbarIcon} /> Do you want to save
            your modification?
          </span>
        }
        action={[
          <>
            <Button
              variant="outlined"
              color="primary"
              onClick={saveRules}
              className={classes.snackbarSaveButton}
            >
              Save
            </Button>
            <Button
              variant="outlined"
              color="secondary"
              onClick={dismissSaveRules}
              className={classes.snackbarCancelButton}
            >
              Cancel
            </Button>
          </>
        ]}
      />
    </Snackbar>
  );
}
