import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%'
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    width: '100%',
    flexShrink: 0
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary
  },
  titleSnippetList: {
    width: 'fit-content',
    margin: 'auto',
    paddingTop: '30px',
    paddingBottom: '1rem',
    fontSize: '22px',
    fontWeight: 700,
    marginBottom: '20px',
    userSelect: 'none'
  },
  highligthTitleSnippetList: {
    width: '100px',
    height: '3px',
    margin: '8px auto 0',
    display: 'block',
    backgroundColor: '#314056'
  }
}));

export default function ControlledExpansionPanels() {
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(false);

  const handleChange = panel => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  return (
    <div className={classes.root}>
      <div className={classes.titleSnippetList}>
        Snippets box
        <span className={classes.highligthTitleSnippetList} />
      </div>
      <ExpansionPanel
        expanded={expanded === 'panel1'}
        onChange={handleChange('panel1')}
      >
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1bh-content"
          id="panel1bh-header"
        >
          <Typography className={classes.heading}>Customize the title</Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <Typography>
            In order to change the default title,
            add the following in the path field: <b>info.title</b>{' '}
            and the following in the content field:
            <blockquote>&#x22;my customized title&#x22;</blockquote>
          </Typography>
        </ExpansionPanelDetails>
      </ExpansionPanel>
      <ExpansionPanel
        expanded={expanded === 'panel2'}
        onChange={handleChange('panel2')}
      >
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel2bh-content"
          id="panel2bh-header"
        >
          <Typography className={classes.heading}>Remove a specific response</Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <Typography style={{ width: '100%' }}>
            In order to remove a specific status code from the responses,
            create a DROP rule and place, for example,
            the following in the path field:{' '}
            <b style={{ fontSize: '12px' }}>paths./products/&#x7B;id&#x7D;.get.responses.401</b>
          </Typography>
        </ExpansionPanelDetails>
      </ExpansionPanel>
      <ExpansionPanel
        expanded={expanded === 'panel3'}
        onChange={handleChange('panel3')}
      >
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel3bh-content"
          id="panel3bh-header"
        >
          <Typography className={classes.heading}>Add a custom query param</Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <Typography style={{ width: '100%' }}>
            In order to add a custom query param on a path,
            add (for example) the following in the path field:{' '}
            <b>paths./orders.get</b>
            and the following in the content field:{' '}
            <blockquote style={{ fontSize: '14px' }}>
              - in: query<br />
              &nbsp;&nbsp;name: sort<br />
              &nbsp;&nbsp;required: false<br />
              &nbsp;&nbsp;schema:<br />
              &nbsp;&nbsp;&nbsp;&nbsp;type: string<br />
              &nbsp;&nbsp;&nbsp;&nbsp;enum:<br />
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;- &#x22;updatedAt:asc&#x22;
            </blockquote>
          </Typography>
        </ExpansionPanelDetails>
      </ExpansionPanel>
    </div>
  );
}
