import React, { Component } from 'react';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import { withStyles } from '@material-ui/core/styles';
import DownloadLink from 'react-download-link';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import {
  IconButton,
  MenuList,
  MenuItem,
  Popper,
  Paper,
  Grow,
  ClickAwayListener
} from '@material-ui/core';
import { UserContext } from '../../contexts/UserContext';
import GitReposAPI from '../../api/GitReposAPI';
import Configuration from '../../helpers/configuration';

const styles = theme => ({
  root: {
    display: 'flex',
    margin: 'auto',
    justifyContent: 'center'
  },
  paper: {
    marginRight: theme.spacing(2)
  },
  menuVersionTextfield: {
    padding: theme.spacing(2),
    paddingTop: 0
  },
  listOrganizationTransfer: {
    overflow: 'auto',
    maxHeight: '200px'
  }
});

class MenuListComposition extends Component {
  static contextType = UserContext;

  state = {
    open: false,
    openDeleteModal: false,
    // anchorEl: '',
    anchorElPub: '',
    gitReposList: []
  };

  onClickRevert() {
    const { revertToThisModel } = this.props;
    revertToThisModel();
    this.handleToggle();
  }

  onClickFreeze() {
    const { freezeModel } = this.props;
    freezeModel();
    this.handleToggle();
  }

  onClickRename() {
    const { renameModel } = this.props;
    renameModel();
    this.handleToggle();
  }

  onClickDelete() {
    const { deleteThisModel } = this.props;
    deleteThisModel();
    this.handleToggleDeleteModel();
  }

  onClickDuplicate() {
    const { duplicateThisModel } = this.props;
    duplicateThisModel();
    this.handleToggle();
  }

  onClickTransfer(modelId, tags, name, organizationId) {
    const { transferThisModel } = this.props;
    transferThisModel(modelId, tags, name, organizationId);
  }

  onClickPublish(repoId, modelId) {
    const { publishThisModel } = this.props;
    publishThisModel(repoId, modelId);
  }

  onClickPublishToApiManager() {
    const { publishToApiManager } = this.props;
    publishToApiManager();
    this.handleToggle()
  }

  handleToggle = () => {
    this.setState(state => ({ open: !state.open }));
  };

  handleToggleDeleteModel = () => {
    this.setState(state => ({
      open: false,
      openDeleteModal: !state.openDeleteModal
    }));
  };

  handleClose = event => {
    if (this.anchorEl.contains(event.target)) {
      return;
    }
    this.setState({ open: false });
    // this.setState({ open: false, openPop: false });
  };

  handleClosePublish = event => {
    if (this.anchorElPub.contains(event.target)) {
      return;
    }
    this.setState({ open: false, openPopPub: false });
  };

  preventForm = e => {
    e.preventDefault();
  };

  handleChange = name => event => {
    this.setState({ [name]: event.target.value });
  };

  // handleClick = event => {
  //   this.setState({ anchorEl: event.currentTarget, openPop: true });
  // };

  handleClickPublish = async event => {
    await GitReposAPI.getRepos(this.props.row.organization.id)
      .then(result => {
        this.setState({ gitReposList: result });
      })
      .catch(() => {});
    this.setState({ anchorElPub: event.currentTarget, openPopPub: true });
  };

  render() {
    const {
      classes,
      isCurrent,
      isNotCurrent,
      downloadFreemind,
      downloadSwagger,
      name
      // user
    } = this.props;
    const {
      open,
      openDeleteModal,
      // anchorEl,
      // openPop,
      anchorElPub,
      openPopPub,
      gitReposList
    } = this.state;

    return (
      <div className={classes.root}>
        <div>
          <IconButton
            data-cy="dashboard-actions-btn"
            size="small"
            buttonRef={node => {
              this.anchorEl = node;
            }}
            aria-label="More"
            aria-owns={open ? 'menu-list-grow' : undefined}
            aria-haspopup="true"
            onClick={this.handleToggle}
          >
            <MoreVertIcon />
          </IconButton>
          <Popper
            className="menu-swagger-list"
            open={open || false}
            anchorEl={this.anchorEl}
            transition
          >
            {({ TransitionProps, placement }) => (
              <Grow
                {...TransitionProps}
                id="menu-list-grow"
                style={{
                  transformOrigin:
                    placement === 'bottom' ? 'center top' : 'center bottom'
                }}
              >
                <Paper>
                  <ClickAwayListener onClickAway={this.handleClose}>
                    <MenuList>
                      <MenuItem
                        disabled={isNotCurrent}
                        onClick={() => this.onClickRename()}
                      >
                        Rename
                      </MenuItem>
                      <MenuItem
                        disabled={isNotCurrent}
                        onClick={() => this.onClickFreeze()}
                      >
                        Freeze model
                      </MenuItem>
                      <MenuItem
                        disabled={isNotCurrent}
                        onClick={() => this.onClickDuplicate()}
                      >
                        Duplicate
                      </MenuItem>
                      <MenuItem
                        disabled={isCurrent}
                        onClick={() => this.onClickRevert()}
                      >
                        Create a new current from this model
                      </MenuItem>
                      <MenuItem>
                        <DownloadLink
                          className="export-button"
                          filename={`${name}_model.mm`}
                          exportFile={() => downloadFreemind()}
                          label="Export to Freemind"
                        />
                      </MenuItem>
                      <MenuItem>
                        <DownloadLink
                          className="export-button"
                          filename={`${name}_swagger.yaml`}
                          exportFile={() => downloadSwagger('YAML_FILE')}
                          label="Download swagger as YAML"
                        />
                      </MenuItem>
                      <MenuItem>
                        <DownloadLink
                          className="export-button"
                          filename={`${name}_swagger.json`}
                          exportFile={() => downloadSwagger('JSON_FILE')}
                          label="Download swagger as JSON"
                        />
                      </MenuItem>
                      {/* {user.organizations.length > 1 ? (
                        <>
                          <MenuItem onClick={this.handleClick}>
                            Transfer to organization
                            <Popper
                              open={openPop}
                              anchorEl={anchorEl}
                              onClose={this.handleClose}
                              placement="right-start"
                            >
                              <Paper
                                className={classes.listOrganizationTransfer}
                              >
                                <MenuList>
                                  {user.organizations
                                    .filter(
                                      organization =>
                                        organization.id !==
                                        this.props.row.organization.id
                                    )
                                    .map(organization => (
                                      <MenuItem
                                        onClick={() =>
                                          this.onClickTransfer(
                                            this.props.row.id,
                                            this.props.row.tags,
                                            this.props.row.name,
                                            organization.id
                                          )
                                        }
                                      >
                                        {organization.name}
                                      </MenuItem>
                                    ))}
                                </MenuList>
                              </Paper>
                            </Popper>
                          </MenuItem>
                        </>
                      ) : null} */}

                      {gitReposList.length > 0 ? (
                        <>
                          <MenuItem onClick={this.handleClickPublish}>
                            Push to repository
                            <Popper
                              open={openPopPub || false}
                              anchorEl={anchorElPub}
                              onClose={this.handleClosePub}
                              placement="right-start"
                            >
                              <Paper
                                className={classes.listOrganizationTransfer}
                              >
                                <MenuList>
                                  {gitReposList.length > 0
                                    ? gitReposList.map(gitRepos => (
                                        <MenuItem
                                          onClick={() =>
                                            this.onClickPublish(
                                              this.props.row.id,
                                              gitRepos.id
                                            )
                                          }
                                        >
                                          {gitRepos.name}
                                        </MenuItem>
                                      ))
                                    : null}
                                </MenuList>
                              </Paper>
                            </Popper>
                          </MenuItem>
                        </>
                      ) : null}
                      {
                        Configuration.GRAVITEE_ENABLED && <MenuItem
                          onClick={() => this.onClickPublishToApiManager()}
                        >
                          Deploy to API Manager
                        </MenuItem>
                      }
                      <MenuItem
                        disabled={isNotCurrent}
                        onClick={() => this.handleToggleDeleteModel()}
                      >
                        Delete
                      </MenuItem>
                    </MenuList>
                  </ClickAwayListener>
                </Paper>
              </Grow>
            )}
          </Popper>
        </div>
        <div>
          <Dialog
            open={openDeleteModal}
            onClose={this.handleClose}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
          >
            <DialogTitle id="alert-dialog-title">
              Delete a model
            </DialogTitle>
            <DialogContent>
              <DialogContentText id="alert-dialog-description">
                Are you sure you want to delete this model?
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button onClick={this.handleToggleDeleteModel} color="primary">
                Cancel
              </Button>
              <Button
                data-cy="delete-model-modal-btn"
                onClick={() => this.onClickDelete()}
                className="danger-btn"
                color="inherit"
                autoFocus
              >
                Delete
              </Button>
            </DialogActions>
          </Dialog>
        </div>
      </div>
    );
  }
}

export default withStyles(styles)(MenuListComposition);
