import React, { useState, useContext } from 'react';
import uuid from 'uuid/v4';
import { Dialog, DialogTitle, IconButton, Chip } from '@material-ui/core';
import { createMuiTheme, makeStyles } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';
import CancelIcon from '@material-ui/icons/Cancel';
import Slide from '@material-ui/core/Slide';
import { Notification } from 'element-react';
import DialogRule from './DialogRule';
import DialogNoRule from './DialogNoRule';
import Snackbar from './Snackbar';
import ModelAPI from '../../api/ModelAPI';

import { RuleContext } from '../../contexts/RuleContext';

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const theme = createMuiTheme({
  overrides: {
    MuiDialog: {
      paperWidthSm: {
        width: 'fit-content',
        maxWidth: 'none',
        height: 'fit-content',
        maxHeight: 'none'
      },
      paperFullWidth: {
        height: '95%',
        maxHeight: 'fit-content'
      }
    },
    MuiPaper: {
      root: {
        height: '100%'
      }
    },
    MuiContainer: {
      maxWidthSm: {
        maxWidth: 'max-content'
      }
    }
  }
});

const useStyles = makeStyles(() => ({
  buttonCloseDialog: {
    color: 'white',
    position: 'absolute',
    right: '1rem',
    top: '0.130rem'
  },
  dialogHeader: {
    backgroundColor: '#324157',
    color: 'white'
  },
  titleDialogChip: {
    fontWeight: 500,
    marginRight: '1.25rem'
  }
}));

export default function RefineModalView(props) {
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  const [showSnack, setShowSnack] = useState(false);
  const { state, dispatch } = useContext(RuleContext);
  const { updateSwaggerList } = props;
  const { id, name, refinerRules } = props.row;
  const { currentRule, rules, isRulesSaved } = state;

  const addRule = () => {
    const createdRule = {
      id: uuid(),
      name: '',
      path: '',
      operation: 'ADD',
      yamlRule: ''
    };
    dispatch({ type: 'addRule', payload: createdRule });
    dispatch({ type: 'selectRule', payload: createdRule });
  };

  const deleteRule = rule => {
    dispatch({ type: 'deleteRule', payload: rule });
    dispatch({ type: 'selectRule', payload: rules[rules.length - 2] });
  };

  const onChangeCurrentRule = event => {
    dispatch({ type: 'updateCurrentRule', payload: event.target });
    dispatch({ type: 'saveCurrentRule', payload: currentRule });
  };

  const onChangeCurrentRuleText = text => {
    dispatch({ type: 'updateCurrentRuleText', payload: text });
    dispatch({ type: 'saveCurrentRule', payload: currentRule });
  };

  const handleClose = () => {
    if (isRulesSaved) {
      setShowSnack(false);
      setOpen(false);
      updateSwaggerList();
    } else {
      setShowSnack(true);
    }
  };

  const handleClickOpen = async () => {
    try {
      const fetchedRules = await ModelAPI.getModelRules(id);
      dispatch({ type: 'fetchRules', payload: fetchedRules });
      setOpen(true);
      dispatch({ type: 'selectRule', payload: fetchedRules[0] });
    } catch (err) {
      Notification.error({
        title: 'Error',
        message: 'You can only modify your own models. Please ask your team leader if you need a modification on this model.'
      });
    }
  };

  const testRules = async () => {
    try {
      await ModelAPI.testModelRule(id, rules);
      Notification.success({
        title: 'Success',
        message: 'All tests have passed.'
      });
    } catch (err) {
      Notification.error({
        title: 'Error',
        message: "The tests didn't pass"
      });
    }
  };

  const saveRules = async () => {
    try {
      await ModelAPI.createModelRule(id, rules);
      dispatch({ type: 'save', payload: true });
      setShowSnack(false);
      Notification.success({
        title: 'Success',
        message: 'The rules have been saved'
      });
    } catch (err) {
      Notification.error({
        title: 'Error',
        message: 'An error occurred while saving the rules'
      });
    }
  };

  const dismissSaveRules = () => {
    dispatch({ type: 'save', payload: true });
    setShowSnack(false);
  };

  return (
    <div>
      <ThemeProvider theme={theme}>
        <Chip
          color="primary"
          size="small"
          onClick={handleClickOpen}
          label={
            refinerRules.length > 1
              ? `${refinerRules.length} Rules`
              : `${refinerRules.length} Rule`
          }
        />
        <Dialog
          fullScreen
          open={open}
          onClose={handleClose}
          maxWidth="lg"
          fullWidth
          TransitionComponent={Transition}
        >
          <DialogTitle id="alert-dialog-title" className={classes.dialogHeader}>
            <Chip
              className={classes.titleDialogChip}
              label={`${name.toUpperCase()}`}
            />
            <IconButton
              edge="end"
              aria-label="delete"
              onClick={handleClose}
              className={classes.buttonCloseDialog}
            >
              <CancelIcon fontSize="large" />
            </IconButton>
          </DialogTitle>

          {rules && rules.length > 0 ? (
            <DialogRule
              name={name}
              addRule={addRule}
              deleteRule={deleteRule}
              saveRules={saveRules}
              testRules={testRules}
              onChange={onChangeCurrentRule}
              onChangeTextEditor={onChangeCurrentRuleText}
              handleClose={handleClose}
            />
          ) : (
            <DialogNoRule
              name={name}
              addRule={addRule}
              saveRules={saveRules}
              handleClose={handleClose}
            />
          )}
          <Snackbar
            showSnack={showSnack}
            saveRules={saveRules}
            dismissSaveRules={dismissSaveRules}
          />
        </Dialog>
      </ThemeProvider>
    </div>
  );
}
