import BaseAPI from './BaseAPI';

export default class ModelTagsAPI extends BaseAPI {
  static getTags(teamId) {
    const params = teamId ? { params: { teamId } } : {};
    return this.execute('get', '/users/me/tags', params);
  }

  static createTag(tag, modelId) {
    return this.execute('post', `/models/${modelId}/tags?input=${tag}`);
  }

  static deleteTag(tag, modelId) {
    return this.execute('delete', `/models/${modelId}/tags?input=${tag}`);
  }
}
