import React, { PureComponent } from 'react';
import { withStyles } from '@material-ui/core/styles';
import { Notification } from 'element-react';
import SwaggerForm from '../dashboard/SwaggerForm';
import SwaggerList from '../dashboard/SwaggerList';
import styles from '../../styles/Dashboard.css';
import { UserContext } from '../../contexts/UserContext';

class Dashboard extends PureComponent {
  static contextType = UserContext;

  render() {
    const { classes, location } = this.props;

    if (location && location.state && location.state.error) {
      Notification.error({
        title: 'Error',
        message: location.state.error
      });
    }

    return (
      <div className={classes.dashboardContainer}>
        <div className={classes.dashboardLateralMenu}>
          <SwaggerForm />
        </div>
        <div className={classes.dashboardSwaggerList}>
          <SwaggerList />
        </div>
      </div>
    );
  }
}

export default withStyles(styles)(Dashboard);
