/* eslint-disable class-methods-use-this */
/* eslint-disable no-param-reassign */
/* eslint-disable no-nested-ternary */
/* eslint-disable react/no-array-index-key */
/* eslint-disable jsx-a11y/label-has-for */
/* eslint-disable jsx-a11y/label-has-associated-control */
/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
import React, { Component } from 'react';
import { Notification } from 'element-react';
import { withRouter } from 'react-router-dom';
import { withStyles } from '@material-ui/core/styles';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import InputBase from '@material-ui/core/InputBase';
import Input from '@material-ui/core/Input';
import AutosizeInput from 'react-input-autosize';
import TablePagination from '@material-ui/core/TablePagination';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Chip from '@material-ui/core/Chip';
import Avatar from '@material-ui/core/Avatar';
import AddFilterIcon from '@material-ui/icons/AddCircleOutline';
import Modal from '@material-ui/core/Modal';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Spinner from '../common/Spinner';
import SwaggerMenu from './SwaggerMenu';
import FilterTag from './FilterTag';
import RefineView from '../refine/RefineModalView';
import FreemindAPI from '../../api/FreemindAPI';
import ModelAPI from '../../api/ModelAPI';
import ModelTagsAPI from '../../api/ModelTagsAPI';
import GitReposAPI from '../../api/GitReposAPI';
import styles from '../../styles/SwaggerList.css';
import { UserContext } from '../../contexts/UserContext';
import { RuleContextProvider } from '../../contexts/RuleContext';
import Configuration from '../../helpers/configuration';

const uuidv4 = require('uuid/v4');

const BootstrapInput = withStyles(theme => ({
  root: {
    width: '100%',
    marginTop: theme.spacing(3)
  },
  input: {
    borderRadius: 4,
    position: 'relative',
    backgroundColor: theme.palette.background.paper,
    border: '1px solid #ced4da',
    fontSize: 16,
    minWidth: '15rem',
    width: '25rem',
    maxWidth: '25rem',
    padding: '10px 26px 10px 12px',
    transition: theme.transitions.create(['border-color', 'box-shadow']),
    table: {
      minWidth: 700
    },
    '&:hover': {
      border: '1px solid black'
    },
    '&:focus': {
      borderRadius: '4px',
      backgroundColor: theme.palette.background.paper,
    }
  }
}))(InputBase);

class SwaggerList extends Component {
  static contextType = UserContext;

  constructor(props) {
    super(props);
    this.state = {
      name: '',
      tagsList: [],
      tags: [],
      pageSize: 20,
      page: 0,
      total: 0,
      models: [],
      modelNames: [],
      showMe: false,
      labelId: '',
      labelInput: '',
      isClicked: true,
      isClickID: '',
      tagName: '',
      modelLoading: true,
      showGravitee: false,
      graviteeMessage: null
    };
    this.inputLabel = null;
    this.inputTag = null;
  }

  componentDidMount() {
    const { selectedTeam } = this.context;
    const teamId = selectedTeam ? selectedTeam.id : '';
    this.updateSwaggerList();
    this.loadOptionModel(teamId);
    this.getUserModelsTags(teamId);
  }

  setFocus() {
    this.inputLabel.focus();
  }

  setFocusTag() {
    this.inputTag.focus();
  }

  getUserModelsTags = (teamId) => {
    ModelTagsAPI.getTags(teamId)
      .then(tagsList => {
        this.setState({ tagsList });
      });
  }

  downloadSwagger = (model, fileType) => {
    return new Promise(resolve => {
      ModelAPI.getModelSwaggerAsFile(model.id, fileType).then(result => {
        resolve(fileType === 'JSON_FILE' ? JSON.stringify(result) : result);
      });
    });
  };

  downloadFreemind = model => {
    return new Promise(resolve => {
      FreemindAPI.getFreemind(model.id).then(data => {
        resolve(data);
      });
    });
  };

  editMindmap = model => {
    this.props.history.push({
      pathname: '/tree',
      state: {
        modelId: model.id,
        organizationId: model.organization.id,
        teamId: model.team.id,
        teamName: model.team.name
      }
    });
  };

  frozenMindmap = model => {
    ModelAPI.getModel(model.id).then(populatedModel => {
      this.props.history.push({
        pathname: '/tree-frozen',
        state: {
          model: populatedModel
        }
      });
    });
  };

  freezeModel = model => {
    ModelAPI.freezeModel(model.id)
      .then(() => {
        this.updateSwaggerList();
        this.loadOptionModel();
        Notification.success({
          title: 'Success',
          message: 'A frozen model has been created.'
        });
      })
      .catch(err => {
        if (err.response.status === 403) {
          Notification.error({
            title: 'Error',
            message:
              'You can only modify your own models. Please ask your team leader if you need a modification on this model.'
          });
        } else {
          Notification.error({
            title: 'Error',
            message: 'An error occurred while freezing model.'
          });
        }
      });
  };

  revertToThisModel = model => {
    ModelAPI.revertModel(model.id)
      .then(() => {
        this.updateSwaggerList();
        this.loadOptionModel();
        Notification.success({
          title: 'Success',
          message: 'Current model has been reverted to this tagged model.'
        });
      })
      .catch(() => {
        Notification.error({
          title: 'Error',
          message: 'An error occurred while reverting'
        });
      });
  };

  transferThisModel = (modelId, tags, name, organizationId) => {
    ModelAPI.changeOrganizationModel(modelId, tags, name, organizationId)
      .then(() => {
        this.updateSwaggerList();
        this.loadOptionModel();
        Notification.success({
          title: 'Success',
          message: 'This model has been transferred.'
        });
      })
      .catch(() => {
        Notification.error({
          title: 'Error',
          message: 'An error occurred while transferring.'
        });
      });
  };

  publishThisModel = (repoId, modelId) => {
    GitReposAPI.commitModelToRepos(repoId, modelId)
      .then(() => {
        Notification.success({
          title: 'Success',
          message: 'This model have been publish to git.'
        });
      })
      .catch(() => {
        Notification.error({
          title: 'Error',
          message: 'An error occurred while publishing to git.'
        });
      });
  };

  deleteThisModel = model => {
    ModelAPI.deleteModel(model.id)
      .then(() => {
        this.updateSwaggerList();
        this.loadOptionModel();
        Notification.success({
          title: 'Success',
          message: 'This model has been deleted.'
        });
      })
      .catch(err => {
        if (err.response.status === 403) {
          Notification.error({
            title: 'Error',
            message:
              'You can only delete your own models. Please ask your team leader if you need to delete this model.'
          });
        } else {
          Notification.error({
            title: 'Error',
            message: 'An error occurred while deleting.'
          });
        }
      });
  };

  publishToApiManager = async (model, env) => {
    try {
      const api = await ModelAPI.publishToApiManager(model.id, env);
      this.updateSwaggerList();
      this.setState({
        showGravitee: true,
        graviteeMessage: api.message
      });
    } catch (error) {
      this.setState({
        showGravitee: true,
        graviteeMessage: error.response.data.message
      });
    }
  };

  closeGraviteeModal = () => {
    this.setState({
      showGravitee: false,
      graviteeMessage: null
    });
  };

  handleNameChange = event => {
    this.setState({ name: event.target.value, page: 0 }, () => {
      this.updateSwaggerList();
    });
  };

  handleTeamChange = ({ target }) => {
    this.setState(
      {
        name: '',
        tags: [],
        page: 0
      },
      () => {
        this.context.selectedTeam = target.value;
        this.loadOptionModel(target.value.id || target.value);
        this.getUserModelsTags(target.value.id || target.value);
        this.updateSwaggerList();
      }
    );
  };

  handleTagsChange = tags => {
    this.setState({ tags, page: 0 }, () => {
      this.updateSwaggerList();
    });
  };

  handleChangePage = (event, page) => {
    this.setState({ page }, () => {
      this.updateSwaggerList();
    });
  };

  handleChangeRowsPerPage = event => {
    this.setState({ pageSize: event.target.value, page: 0 }, () => {
      this.updateSwaggerList();
    });
  };

  loadOptionModel = teamId => {
    ModelAPI.getModelNames(teamId).then(resp => {
      this.setState({
        modelNames: resp.map(el => ({ id: uuidv4(), name: el }))
      });
    });
  };

  formPreventDefault = e => {
    e.preventDefault();
  };

  formSubmit(model, target) {
    this.setState({ showMe: false });
    if (model.name !== target.value) {
      this.renameThisModel(model, target.value);
    }
  }

  formSubmitTag(model, target) {
    this.createTag(model, target.value);
  }

  inputName(row) {
    const { classes } = this.props;
    if (this.state.labelId === row.id && this.state.showMe) {
      return (
        <form onSubmit={() => this.formSubmit(row, this.inputLabel)}>
          <Input
            name="Rename"
            // eslint-disable-next-line no-return-assign
            inputRef={elem => (this.inputLabel = elem)}
            id={row.id}
            label="name"
            type="name"
            onChange={e => this.changeLabel(e.target)}
            value={this.state.labelInput}
            onBlur={e => this.hideMe(e.target, row)}
          />
        </form>
      );
    }
    if (row.freezeTag === '0') {
      return (
        <div
          onClick={() => this.editMindmap(row)}
          style={{
            verticalAlign: 'middle',
            cursor: 'pointer'
          }}
        >
          <div className={classes.ellipsis}>
            <label
              disabled
              style={{
                cursor: 'pointer',
                textDecoration: 'underline'
              }}
            >
              {row.name}
            </label>
          </div>
        </div>
      );
    }
    return (
      <div
        onClick={() => this.frozenMindmap(row)}
        style={{
          verticalAlign: 'middle',
          textOverflow: 'ellipsis',
          whiteSpace: 'nowrap',
          overflow: 'hidden',
          cursor: 'pointer'
        }}
      >
        <div className={classes.ellipsis}>
          <label
            disabled
            style={{
              cursor: 'pointer',
              textDecoration: 'underline'
            }}
          >
            {row.name}
          </label>
        </div>
      </div>
    );
  }

  displayFormattedDate(freezeTag, date) {
    return freezeTag === '0' ? (
      <p>{this.formatDate(date)}</p>
    ) : (
      <p style={{ color: '#2a8dff' }}>
        {this.formatDate(date)}
      </p>
    );
  }

  formatDate(dateStr) {
    const date = new Date(dateStr);
    return `${date.toLocaleDateString('en-US')} ${date.toLocaleTimeString(
      'en-US',
      {
        hour: 'numeric',
        minute: 'numeric'
      }
    )}`;
  }

  renameModel(row) {
    this.revealMe(row.id, row.name, row.freezeTag);
    this.setState({ showMe: true });
  }

  updateSwaggerList() {
    this.setState({
      modelLoading: true
    });

    const {
      page,
      pageSize,
      name,
      tags
    } = this.state;

    const { selectedTeam } = this.context;

    const mapName = [];

    const params = { page, pageSize };

    if (name) params.name = name;
    if (tags && tags.length > 0) params.tags = tags.join(',');
    if (selectedTeam) params.team = selectedTeam.id;

    ModelAPI.getModels(params)
      .then(response => {
        if (response.data.data) {
          mapName.push(response.data.data);
        }

        this.setState({
          models: mapName.flat(),
          total: response.data.total,
          pageSize: response.data.pageSize,
          modelLoading: false
        });
      })
      .catch(() => {
        this.setState({
          models: [],
          total: 0,
          pageSize: 0,
          modelLoading: false
        });
      });
  }

  revealMe(id, label, freezeTag) {
    if (freezeTag === '0') {
      this.setState({ showMe: true, labelId: id, labelInput: label }, () =>
        this.setFocus()
      );
    }
  }

  hideMe(target, data) {
    this.setState({ showMe: false }, () =>
      this.renameThisModel(data, target.value)
    );
  }

  changeLabel(target) {
    this.setState({
      labelInput: target.value
    });
  }

  renameThisModel(model, value) {
    ModelAPI.renameModel(model.id, value)
      .then(() => {
        this.updateSwaggerList();
        this.loadOptionModel();
      })
      .catch(err => {
        if (err.response.status === 403) {
          Notification.error({
            title: 'Error',
            message:
              'You can only modify your own models. Please ask your team leader if you need a modification on this model.'
          });
        } else {
          Notification.error({
            title: 'Error',
            message: 'A model already have this name.'
          });
        }
      });
  }

  duplicateThisModel(model) {
    ModelAPI.duplicateModel(model.id)
      .then(() => {
        this.updateSwaggerList();
        this.loadOptionModel();
        Notification.success({
          title: 'Success',
          message: 'This model has been duplicated.'
        });
      })
      .catch(err => {
        if (err.response.status === 403) {
          Notification.error({
            title: 'Error',
            message:
              'You can only modify your own models. Please ask your team leader if you need a modification on this model.'
          });
        } else {
          Notification.error({
            title: 'Error',
            message: 'An error occurred while duplicating.'
          });
        }
      });
  }

  handleDelete(e) {
    this.setState({ isClicked: false, isClickID: e }, () => this.setFocusTag());
  }

  deleteTag(tag, model) {
    ModelTagsAPI.deleteTag(tag, model.id)
      .then(() => {
        this.updateSwaggerList();
      })
      .catch(err => {
        if (err.response.status === 403) {
          Notification.error({
            title: 'Error',
            message:
              'You can only modify your own models. Please ask your team leader if you need a modification on this model.'
          });
        } else {
          Notification.error({
            title: 'Error',
            message: 'An error occurred while deleting a tag.'
          });
        }
      });
  }

  cancelTag() {
    this.setState({ isClicked: true, isClickID: null, tagName: '' });
  }

  hideInput(model) {
    this.setState({ isClicked: true, isClickID: null }, () => {
      const { tagName, tagsList } = this.state;
      ModelTagsAPI.createTag(tagName, model.id)
        .then(() => {
          if (!tagsList.includes(tagName)) {
            this.state.tagsList.push(tagName);
          }
          this.setState({ tagName: '' });
          this.updateSwaggerList();
        })
        .catch(err => {
          this.setState({ tagName: '' });
          if (err.response.status === 403) {
            Notification.error({
              title: 'Error',
              message:
                'You can only modify your own models. Please ask your team leader if you need a modification on this model.'
            });
          } else {
            Notification.error({
              title: 'Error',
              message: 'An error occurred while creating a tag.'
            });
          }
        });
    });
  }

  changeTagName(e) {
    this.setState({ tagName: e.target.value });
  }

  render() {
    const { classes } = this.props;
    const { page, pageSize, models, modelLoading, modelNames, tagsList } = this.state;
    const { user } = this.context;
    return (
      <div className={classes.swaggerList} onSubmit={this.formPreventDefault}>
        <h2 className={`${classes.dashboardH2Titles} ${classes.modelPaddingTitle}`}>Models List</h2>
        <div className={classes.swaggerSelect}>
          { !Configuration.GRAVITEE_ENABLED && <FormControl className={classes.searchSelect}>
              <InputLabel
                htmlFor="age-customized-select-team-name"
                className={classes.bootstrapFormLabel}
              />
              <Select
                displayEmpty
                style={{ margin: 0 }}
                value={this.context.selectedTeam || ''}
                onChange={this.handleTeamChange}
                input={
                  <BootstrapInput name="team" id="age-customized-select-team-name" />
                }
              >
                <MenuItem value="">
                  <em>All teams</em>
                </MenuItem>
                {this.context.teams.map(t => {
                  return (
                    <MenuItem key={t.team.id} value={t.team}>
                      {t.team.name}
                    </MenuItem>
                  );
                })}
              </Select>
            </FormControl>
          }
          <FormControl className={classes.searchSelect}>
            <InputLabel
              htmlFor="age-customized-select-models-name"
              className={classes.bootstrapFormLabel}
            />
            <Select
              displayEmpty
              data-cy="model-name-from-models-select"
              style={{ margin: 0 }}
              value={this.state.name}
              onChange={this.handleNameChange}
              input={
                <BootstrapInput name="name" id="age-customized-select-models-name" />
              }
            >
              <MenuItem value="">
                <em>All models</em>
              </MenuItem>
              {modelNames.sort(this.compareNameList).map(e => {
                return (
                  <MenuItem key={e.id} value={e.name}>
                    {e.name}
                  </MenuItem>
                );
              })}
            </Select>
          </FormControl>
          <FilterTag tagsList={tagsList} handleTagsChange={tags => this.handleTagsChange(tags)} />
        </div>

        <Paper className={`${classes.root} ${classes.paper}`} >
          <Table className={classes.table} size="small">
            <TableHead>
              <TableRow>
                <TableCell className={classes.tableHeaderCell} align="left">Model</TableCell>
                <TableCell className={classes.tableHeaderCell} align="center">Creation</TableCell>
                <TableCell className={classes.tableHeaderCell} align="center">Last Modifications</TableCell>
                <TableCell className={classes.tableHeaderCell} align="center">Team</TableCell>
                <TableCell className={classes.tableHeaderCell} align="center">Tags</TableCell>
                <TableCell className={classes.tableHeaderCell} align="center">Refining</TableCell>
                <TableCell className={classes.tableHeaderCell} align="center">Actions</TableCell>
              </TableRow>
            </TableHead>
            <Spinner loading={modelLoading}>
              <TableBody>
                {models.length === 0 && (
                  <TableRow>
                    <TableCell size="medium" align="center" component="th" colSpan={7} style={{ fontSize: '18px' }}>
                      <p>No models in selection.</p>
                    </TableCell>
                  </TableRow>
                )}
                {models.map(row => {
                  return (
                    <TableRow key={row.id} hover>
                      <TableCell className={classes.tableBodyCell} style={{ width: '12rem' }} key={row.id} data-cy="model-name-from-swagger-list" align="left">
                        <p style={{ width: '12rem' }}>{this.inputName(row)}</p>
                      </TableCell>
                      <TableCell className={classes.tableBodyCell} align="center">
                        <p>{row.author.username}</p>
                        {this.displayFormattedDate(row.freezeTag, row.createdAt)}
                      </TableCell>
                      <TableCell className={classes.tableBodyCell} align="center">
                        {
                          row.freezeTag === '0' ?
                              <div>
                                <p>{row.lastUpdatedUser || row.author.username}</p>
                                {this.displayFormattedDate(row.freezeTag, row.updatedAt)}
                              </div>
                            :
                              <p style={{ color: '#2a8dff' }}>This model is Frozen</p>
                        }
                      </TableCell>
                      <TableCell className={classes.tableBodyCell} align="center"><p>{row.team.name}</p></TableCell>
                      <TableCell align="center">
                        {row.tags.map(tag => {
                          return tag.startsWith('version:') ? (
                            <Chip
                              key={tag}
                              className={classes.chip}
                              avatar={<Avatar>V</Avatar>}
                              label={tag.split(':')[1]}
                              onDelete={() => this.deleteTag(tag, row)}
                              size="small"
                              clickable={false}
                            />
                          ) : (
                            <Chip
                              key={tag}
                              className={classes.chip}
                              label={tag}
                              onDelete={() => this.deleteTag(tag, row)}
                              size="small"
                              clickable={false}
                            />
                          );
                        })}
                        {this.state.isClicked ? (
                          <AddFilterIcon
                            onClick={() => this.handleDelete(row.id)}
                            className={classes.filterIcon}
                          />
                        ) : this.state.isClickID === row.id ? (
                          <form onSubmit={() => this.hideInput(row)}>
                            <AutosizeInput
                              name="Rename"
                              // eslint-disable-next-line no-return-assign
                              inputRef={elem => (this.inputTag = elem)}
                              id={row.id}
                              inputClassName={classes.inputTag}
                              label="name"
                              type="name"
                              onChange={e => this.changeTagName(e)}
                              value={this.state.tagName}
                              onBlur={() => this.cancelTag()}
                            />
                          </form>
                        ) : (
                          <AddFilterIcon
                            onClick={() => this.handleDelete(row.id)}
                            className={classes.filterIcon}
                          />
                        )}
                      </TableCell>
                      <TableCell align="center">
                        <RuleContextProvider>
                          <RefineView
                            row={row}
                            updateSwaggerList={() => this.updateSwaggerList()}
                          />
                        </RuleContextProvider>
                      </TableCell>
                      <TableCell align="right">
                        <SwaggerMenu
                          user={user}
                          row={row}
                          isNotCurrent={row.freezeTag !== '0'}
                          isCurrent={row.freezeTag === '0'}
                          name={row.name}
                          renameModel={() => this.renameModel(row)}
                          nameVersion={() => this.nameVersion(row)}
                          freezeModel={() => this.freezeModel(row)}
                          downloadFreemind={() => this.downloadFreemind(row)}
                          publishThisModel={(repoId, modelId) =>
                            this.publishThisModel(repoId, modelId)
                          }
                          publishToApiManager={() =>
                            this.publishToApiManager(row)
                          }
                          downloadSwagger={fileType =>
                            this.downloadSwagger(row, fileType)
                          }
                          revertToThisModel={() =>
                            this.revertToThisModel(row)
                          }
                          transferThisModel={(
                            modelId,
                            tags,
                            name,
                            organizationId
                          ) =>
                            this.transferThisModel(
                              modelId,
                              tags,
                              name,
                              organizationId
                            )
                          }
                          deleteThisModel={() => this.deleteThisModel(row)}
                          duplicateThisModel={() =>
                            this.duplicateThisModel(row)
                          }
                        />
                      </TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
            </Spinner>
          </Table>
        </Paper>
        <div className={classes.paginationBlock}>
          <TablePagination
            className={classes.tablePagination}
            rowsPerPageOptions={[20, 30, 50]}
            component="div"
            page={page}
            count={this.state.total}
            rowsPerPage={pageSize}
            backIconButtonProps={{
              'aria-label': 'Previous Page'
            }}
            nextIconButtonProps={{
              'aria-label': 'Next Page'
            }}
            onChangePage={this.handleChangePage}
            onChangeRowsPerPage={this.handleChangeRowsPerPage}
          />
        </div>
        <Modal
          open={this.state.showGravitee}
          onClose={() => this.closeGraviteeModal()}
        >
          {this.state.graviteeMessage && (
            <div className={classes.modalContainer}>
              <Typography variant="h6">{this.state.graviteeMessage}</Typography>

              <Button
                className={classes.buttonGravitee}
                color="primary"
                variant="contained"
                onClick={() => this.closeGraviteeModal()}
              >
                OK
              </Button>
            </div>
          )}
        </Modal>
      </div>
    );
  }
}

export default withRouter(withStyles(styles)(SwaggerList));
