import React, { Component } from 'react';
import Autocomplete from '@material-ui/lab/Autocomplete';
import TextField from '@material-ui/core/TextField';
import { withStyles } from '@material-ui/core/styles';

const styles = () => ({
  root: {
    width: '20rem',
    backgroundColor: '#fff',
    '& > div > div > span': {
      padding: '2px 5px',
      fontWeight: 'normal',
      borderRadius: '16px'
    },
  },
  tag: {
    backgroundColor: '#4cab90',
    color: 'white',
    fontWeight: 'bold'
  },
});

class IntegrationAutosuggest extends Component {
  handleInputChange = (event, values) => {
    this.props.handleTagsChange(values);
  };

  render() {
    const { classes, tagsList } = this.props;

    return (
      <Autocomplete
        key={tagsList}
        autoHighlight
        multiple
        limitTags={2}
        id="multiple-limit-tags"
        onChange={this.handleInputChange}
        size="small"
        classes={{
          root: classes.root,
          tag: classes.tag
        }}
        options={tagsList}
        getOptionLabel={(option) => option}
        renderInput={(params) => (
          <TextField {...params} variant="outlined" label="Search by Tags" placeholder="Tags" />
        )}
      />
    );
  }
}

export default withStyles(styles)(IntegrationAutosuggest);
