/* eslint-disable react/no-array-index-key */
import React, { useContext } from 'react';
import { Card, List, ListItem, Button, Typography } from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import RemoveIcon from '@material-ui/icons/Remove';
import DeleteIcon from '@material-ui/icons/Delete';
import { makeStyles, withStyles } from '@material-ui/core/styles';

import { RuleContext } from '../../contexts/RuleContext';

const StyledListItem = withStyles({
  root: {
    "&$selected": {
      // backgroundColor: ""
    },
    borderBottom: '1px solid rgba(0, 0, 0, 0.1)'
  },
  selected: {}
})(ListItem);

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    height: '100%'
  },
  demo: {
    backgroundColor: theme.palette.background.paper,
    height: '100%'
  },
  title: {
    margin: theme.spacing(4, 0, 2)
  },
  buttonAddRule: {
    margin: 'auto',
    display: 'flex',
    width: '-webkit-fill-available',
    color: 'gray'
  },
  ruleContainer: {
    height: '100%',
    overflow: 'auto'
  },
  operationIcon: {
    marginRight: '0.5rem'
  },
  titleRuleList: {
    width: 'fit-content',
    margin: 'auto',
    paddingTop: '30px',
    paddingBottom: '1rem',
    fontSize: '22px',
    fontWeight: 700,
    marginBottom: '20px',
    userSelect: 'none'
  },
  highligthTitleRuleList: {
    width: '100px',
    height: '3px',
    margin: '8px auto 0',
    display: 'block',
    backgroundColor: '#314056'
  },
  ruleName: {
    fontWeight: '300',
    width: '90%'
  }
}));

export default function InteractiveList(props) {
  const classes = useStyles();
  const { state, dispatch } = useContext(RuleContext);
  const [selectedIndex, setSelectedIndex] = React.useState(0);
  const { rules } = state;
  const { addRule, deleteRule } = props;

  const handleListItemClick = (index, rule) => {
    setSelectedIndex(index);
    dispatch({ type: 'selectRule', payload: rule });
  };

  const handleAddItem = () => {
    setSelectedIndex(rules.length);
    addRule();
  };

  return (
    <div className={classes.root}>
      <div className={classes.demo}>
        <Card className={classes.ruleContainer}>
          <div className={classes.titleRuleList}>
            Refine rules
            <span className={classes.highligthTitleRuleList} />
          </div>
          <List dense>
            {rules.length > 0 &&
              rules.map((rule, index) => (
                <StyledListItem
                  key={index}
                  button
                  selected={selectedIndex === index}
                  onClick={() => handleListItemClick(index, rule)}
                >
                  {rule.operation === 'ADD' ? (
                    <AddIcon
                      color="secondary"
                      className={classes.operationIcon}
                    />
                  ) : (
                    <RemoveIcon
                      color="primary"
                      className={classes.operationIcon}
                    />
                  )}
                  <Typography
                    color={rule.operation === 'ADD' ? 'secondary' : 'primary'}
                    className={classes.ruleName}
                  >
                    {rule.name}
                  </Typography>
                  <div style={{ textAlign: 'right', width: '10%' }}>
                    <DeleteIcon
                      className="danger-btn"
                      onClick={() => deleteRule(rule)}
                    />
                  </div>
                </StyledListItem>
              ))}
          </List>
          <Button
            color="secondary"
            onClick={() => handleAddItem()}
            className={classes.buttonAddRule}
          >
            Create a new rule
          </Button>
        </Card>
      </div>
    </div>
  );
}
