import BaseAPI from './BaseAPI';

export default class SwaggerAPI extends BaseAPI {
  static getResourcesFromUploaded(data) {
    return this.execute('post', '/swaggerUpload/resources', {
      data: { jsonstring: data }
    });
  }

  static getModelsFromResources(data) {
    return this.execute('post', '/swaggerUpload/models', {
      data
    });
  }

  static requestPreview(data) {
    return this.execute('post', '/swaggerPreviews', { data });
  }

  static requestLivePreview(data, organizationId) {
    return this.execute(
      'post',
      `/swaggerLivePreviews?organizationId=${organizationId}`,
      { data }
    );
  }
}
