import React from 'react';
import {
  Container,
  DialogContent,
  Typography,
  Button
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(() => ({
  container: {
    display: 'grid',
    gridTemplateColumns: '25% 55% 20%',
    gridTemplateRows: '20% 60% 20%',
    gridGap: '0.1rem',
    height: '100%'
  },
  containerDialogNoRule: {
    gridRow: '1/4',
    gridColumn: '1 / 4',
    margin: 'auto',
    textAlign: 'center'
  },
  buttonAddRule: {
    margin: '5rem auto'
  }
}));

export default function DialogNoRule(props) {
  const classes = useStyles();
  const { addRule } = props;

  return (
    <>
      <DialogContent>
        <Container className={classes.container}>
          <Container className={classes.containerDialogNoRule}>
            <Typography variant="h4" component="h1">
              There are no rules on this model
            </Typography>

            <Typography variant="body1" component="p">
              {`Using "refiners", you can customize the generated swaggers.\n
              Adding, modifying and removing lines is possible.\n So in case one
              feature is missing or does not meet your needs, it can be easily
              adjusted here.\n For example one can add custom security settings,
              an introductory text, or remove an unwanted response status, etc.\n
              This does not affect the models: the refinements defined here act
              as a presentation layer above the generated swaggers.\n
              Reciprocally, modifying the models afterwards will not alter the
              refiners.`}
            </Typography>

            <Button
              color="secondary"
              variant="contained"
              onClick={addRule}
              className={classes.buttonAddRule}
            >
              Create my first rule
            </Button>
          </Container>
        </Container>
      </DialogContent>
    </>
  );
}
