import React, { useContext } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
  Radio,
  Chip,
  RadioGroup,
  FormControlLabel,
  FormControl
} from '@material-ui/core';

import { RuleContext } from '../../contexts/RuleContext';

const useStyles = makeStyles({
  root: {
    '&:hover': {
      backgroundColor: 'transparent',
      margin: 'auto'
    }
  },
  icon: {
    borderRadius: '50%',
    width: 16,
    height: 16,
    boxShadow:
      'inset 0 0 0 1px rgba(16,22,26,.2), inset 0 -1px 0 rgba(16,22,26,.1)',
    backgroundColor: '#f5f8fa',
    backgroundImage:
      'linear-gradient(180deg,hsla(0,0%,100%,.8),hsla(0,0%,100%,0))',
    '$root.Mui-focusVisible &': {
      outline: '2px auto rgba(19,124,189,.6)',
      outlineOffset: 2
    },
    'input:hover ~ &': {
      backgroundColor: '#ebf1f5'
    },
    'input:disabled ~ &': {
      boxShadow: 'none',
      background: 'rgba(206,217,224,.5)'
    }
  },
  checkedIcon: {
    backgroundColor: '#137cbd',
    backgroundImage:
      'linear-gradient(180deg,hsla(0,0%,100%,.1),hsla(0,0%,100%,0))',
    '&:before': {
      display: 'block',
      width: 16,
      height: 16,
      backgroundImage: 'radial-gradient(#fff,#fff 28%,transparent 32%)',
      content: '""'
    },
    'input:hover ~ &': {
      backgroundColor: '#106ba3'
    }
  },
  formContainer: {
    margin: 'auto'
  },
  formLabel: {
    margin: 0
  }
});

function StyledRadio(props) {
  const classes = useStyles();
  const { label } = props;

  return (
    <Radio
      className={classes.root}
      disableRipple
      color="default"
      checkedIcon={
        <Chip
          color={label === 'DROP' ? 'primary' : 'secondary'}
          label={label}
        />
      }
      icon={
        <Chip
          variant="outlined"
          color={label === 'DROP' ? 'primary' : 'secondary'}
          label={label}
        />
      }
      {...props}
    />
  );
}

export default function CustomizedRadios(props) {
  const classes = useStyles();
  const { onChange } = props;
  const { state } = useContext(RuleContext);
  const { currentRule } = state;

  return (
    <FormControl component="fieldset" className={classes.formContainer}>
      <RadioGroup
        row
        value={currentRule.operation || 'ADD'}
        aria-label="Operation"
        name="operation"
        onChange={onChange}
        style={{ justifyContent: 'center' }}
      >
        <FormControlLabel
          className={classes.formLabel}
          value="ADD"
          control={<StyledRadio label="ADD" />}
        />
        <FormControlLabel
          className={classes.formLabel}
          value="DROP"
          control={<StyledRadio label="DROP" />}
        />
      </RadioGroup>
    </FormControl>
  );
}
