import React, { createContext, useReducer } from 'react';

const RuleContext = createContext();

const initialState = {
  rules: [],
  currentRule: {},
  isRulesSaved: true,
  openAlert: false
};

const reducer = (state, action) => {
  const { payload, type } = action;
  const { currentRule, rules } = state;

  switch (type) {
    case 'addRule':
      return { ...state, rules: [...rules, payload], isRulesSaved: false };
    case 'deleteRule':
      return {
        ...state,
        rules: [...rules.filter(rule => rule !== payload)],
        isRulesSaved: false
      };
    case 'updateCurrentRule': {
      return {
        ...state,
        currentRule: {
          ...currentRule,
          [payload.name]: payload.value
        },
        rules: [...rules],
        isRulesSaved: false
      };
    }
    case 'updateCurrentRuleText':
      return {
        ...state,
        currentRule: { ...currentRule, yamlRule: payload, isRulesSaved: false }
      };
    case 'saveCurrentRule': {
      const foundIndex = rules.findIndex(x => x.id === currentRule.id);
      const newRules = rules;
      newRules[foundIndex] = currentRule;
      return { ...state, rules: newRules, isRulesSaved: false };
    }
    case 'selectRule':
      return { ...state, currentRule: payload };
    case 'fetchRules':
      return { ...state, rules: payload };
    case 'save':
      return { ...state, isRulesSaved: payload };
    default:
      return { ...state };
  }
};

function RuleContextProvider(props) {
  const [state, dispatch] = useReducer(reducer, initialState);
  const value = { state, dispatch };

  return (
    <RuleContext.Provider value={value}>{props.children}</RuleContext.Provider>
  );
}

const RuleContextConsumer = RuleContext.Consumer;

export { RuleContext, RuleContextProvider, RuleContextConsumer };
