import uniqid from 'uniqid';

export function searchTreeByKey(key, startnode) {
  const stack = [startnode];
  while (stack.length) {
    const node = stack.pop();
    if (node.nodeKey === key) return node;
    stack.push(...node.children);
  }
  return stack.pop() || null;
}

export function isAncestorOf(nodeKey, ofNodeKey, root) {
  let ofNode = searchTreeByKey(ofNodeKey, root);

  while (ofNode.nodeKey !== nodeKey && ofNode.parent) {
    ofNode = searchTreeByKey(ofNode.parent, root);
  }

  return ofNode.nodeKey === nodeKey;
}

export function cloneTree(node, parentNodeKey, generateId = true) {
  return {
    ...node,
    nodeKey: generateId ? uniqid() : node.nodeKey,
    parent: parentNodeKey,
    children: node.children.map(child => {
      return cloneTree(child, node.nodeKey, generateId);
    })
  };
}

export function addNewChildNode(root, selectedNodeKey, direction) {
  if (!selectedNodeKey) {
    return {};
  }
  const allData = cloneTree(root, null, false);
  const nodeWhereToAdd = searchTreeByKey(selectedNodeKey, allData);
  const newNodeKey = uniqid();
  const newNode = {
    name: '',
    direction: direction || nodeWhereToAdd.direction,
    parent: nodeWhereToAdd.nodeKey,
    children: [],
    nodeKey: newNodeKey,
    type: 'string',
    referenceId: '',
    isArray: false,
    example: '',
    abstract: true,
    description: '',
    queryable: false,
    paginable: false,
    patchable: false,
    notImplemented: false,
    isSubPath: false,
    verbs: [],
    calculated: false,
    queryParams: '',
    branchColor: nodeWhereToAdd.branchColor
  };
  nodeWhereToAdd.children.push(newNode);
  nodeWhereToAdd.type = nodeWhereToAdd.type === 'array' ? 'array' : 'object';
  return {
    data: allData,
    selectedNodeKey: newNodeKey,
    node: newNode
  };
}

export function newTree(swaggerName) {
  const parentId = uniqid();
  return {
    name: swaggerName,
    direction: 'right',
    nodeKey: parentId,
    parent: null,
    description: '',
    example: '',
    type: 'string',
    referenceId: '',
    isArray: false,
    abstract: true,
    queryable: false,
    paginable: false,
    patchable: false,
    notImplemented: false,
    isSubPath: false,
    calculated: false,
    queryParams: '',
    verbs: ['SEARCH', 'POST', 'GET', 'PUT', 'DELETE'],
    children: [
      {
        name: 'id',
        direction: 'right',
        description: '',
        type: 'integer',
        referenceId: '',
        isArray: false,
        abstract: true,
        queryable: false,
        paginable: false,
        patchable: false,
        notImplemented: false,
        isSubPath: false,
        calculated: true,
        example: '',
        parent: parentId,
        nodeKey: uniqid(),
        children: []
      },
      {
        name: 'an attribute',
        direction: 'right',
        description: '',
        type: 'integer',
        referenceId: '',
        isArray: false,
        abstract: true,
        queryable: false,
        paginable: false,
        patchable: false,
        notImplemented: false,
        isSubPath: false,
        calculated: true,
        example: '',
        parent: parentId,
        nodeKey: uniqid(),
        children: []
      }
    ]
  };
}

function updateSubNodes(parentNodeToUpdate, changeFunction) {
  changeFunction(parentNodeToUpdate);

  if (parentNodeToUpdate.children && parentNodeToUpdate.children.length > 0) {
    parentNodeToUpdate.children.map(child =>
      updateSubNodes(child, changeFunction)
    );
  }
}

function updateParentNodes(
  root,
  childNode,
  changeFunction,
  changeFunctionParam
) {
  changeFunction(childNode, changeFunctionParam);
  if (childNode.parent) {
    const parent = searchTreeByKey(childNode.parent, root);

    if (parent) {
      updateParentNodes(root, parent, changeFunction, changeFunctionParam);
    }
  }
}

export function updateTree(root, node) {
  const data = cloneTree(root, null, false);
  const nodeToUpdate = searchTreeByKey(node.nodeKey, data);
  nodeToUpdate.name = node.name;
  nodeToUpdate.description = node.description;
  nodeToUpdate.xDescription = node.xDescription;
  nodeToUpdate.example = node.example;
  nodeToUpdate.type = node.type;
  nodeToUpdate.referenceId = node.referenceId;
  nodeToUpdate.isArray = node.isArray;

  nodeToUpdate.queryable = node.queryable;
  nodeToUpdate.isSubPath = node.isSubPath;
  nodeToUpdate.calculated = node.calculated;
  nodeToUpdate.paginable = node.paginable;
  nodeToUpdate.patchable = node.patchable;

  nodeToUpdate.queryParams = node.queryParams;
  nodeToUpdate.verbs = node.verbs;
  nodeToUpdate.parent = node.parent;

  if (nodeToUpdate.type === 'reference') {
    nodeToUpdate.children = node.children;
  }

  if (node.branchColor !== nodeToUpdate.branchColor) {
    updateSubNodes(nodeToUpdate, tmpNode => {
      tmpNode.branchColor = node.branchColor; // eslint-disable-line no-param-reassign
    });
  }

  if (nodeToUpdate.notImplemented !== node.notImplemented) {
    if (node.notImplemented) {
      // Mark all subnodes as not implemented
      updateSubNodes(nodeToUpdate, tmpNode => {
        tmpNode.notImplemented = true; // eslint-disable-line no-param-reassign
      });
    } else {
      // Mark all parent nodes as implemented
      updateParentNodes(data, nodeToUpdate, tmpNode => {
        tmpNode.notImplemented = false; // eslint-disable-line no-param-reassign
      });
    }
  }

  if (nodeToUpdate.abstract !== node.abstract) {
    if (node.abstract) {
      // Add the parent to abstract
      updateParentNodes(data, nodeToUpdate, tmpNode => {
        tmpNode.abstract = true; // eslint-disable-line no-param-reassign
      });
    } else {
      // remove only this from abstract
      nodeToUpdate.abstract = node.abstract; // eslint-disable-line no-param-reassign
    }
  }

  return data;
}
