import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import {
  FormControl,
  Typography,
  RadioGroup,
  FormControlLabel,
  Radio,
  Button
} from '@material-ui/core';
import SwaggerAPI from '../../api/SwaggerAPI';

function getModalStyle() {
  return {
    top: '50%',
    left: `50%`,
    transform: `translate(-50%, -50%)`
  };
}

const useStyles = makeStyles(theme => ({
  root: {
    maxHeight: 500,
    overflow: 'scroll'
  },
  paper: {
    position: 'absolute',
    width: 400,
    maxHeight: '80%',
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3)
  }
}));

export default function ResourceSelector(props) {
  const classes = useStyles();
  const [modalStyle] = React.useState(getModalStyle);
  const [value, setValue] = React.useState('');
  const [invalidForm, setInvalidForm] = React.useState(true);

  function handleChange(event) {
    if (event.target.value) {
      setInvalidForm(false);
    }
    setValue(event.target.value);
  }

  function formHandleClick() {
    SwaggerAPI.getModelsFromResources({
      resource: value,
      paths: props.paths,
      api: props.api
    }).then(data => {
      props.close(data);
    });
  }

  return (
    <Modal
      aria-labelledby="simple-modal-title"
      aria-describedby="simple-modal-description"
      open={props.open}
      onClose={props.close}
    >
      <div style={modalStyle} className={classes.paper}>
        <div className={classes.root}>
          <FormControl component="fieldset" className={classes.formControl}>
            <Typography variant="h5" component="h5" gutterBottom>
              We found several APIs to import!
            </Typography>
            <Typography variant="h7" component="h7" gutterBottom>
              To start, please choose the model you want to import into
              AutoSwagger:
            </Typography>
            <RadioGroup
              aria-label="Resource"
              name="resource"
              className={classes.group}
              value={value}
              onChange={handleChange}
            >
              {props.resources.map(resource => {
                return (
                  <FormControlLabel
                    value={resource}
                    control={<Radio />}
                    label={resource}
                  />
                );
              })}
            </RadioGroup>
          </FormControl>
        </div>
        <Button
          variant="contained"
          color="primary"
          className={`el-button import-button el-button--primary shared-width ${
            invalidForm ? 'is-disabled' : ''
          }`}
          disabled={invalidForm}
          type="button"
          onClick={formHandleClick}
        >
          Import
        </Button>
      </div>
    </Modal>
  );
}
