/* eslint-disable no-param-reassign */
/* eslint-disable react/no-string-refs */
/* eslint-disable react/no-array-index-key */
/* eslint-disable jsx-a11y/label-has-associated-control */
/* eslint-disable jsx-a11y/label-has-for */
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import InputBase from '@material-ui/core/InputBase';
import MenuItem from '@material-ui/core/MenuItem';
import Typography from '@material-ui/core/Typography';
import { Button, TextField } from '@material-ui/core';
import { Notification } from 'element-react';
import { withStyles } from '@material-ui/core/styles';
import Joi from '@hapi/joi';
import FreemindAPI from '../../api/FreemindAPI';
import SwaggerAPI from '../../api/SwaggerAPI';
import globalStyles from '../../styles/global.css';
import JsonExamples from '../../examples/examples.json';
import ResourceSelector from './ResourceSelector';
import TeamSelector from './TeamSelector';
import ModelAPI from '../../api/ModelAPI';
import TeamAPI from '../../api/TeamAPI';
import { UserContext } from '../../contexts/UserContext';
import { newTree } from '../../helpers/tree_helper';
import Configuration from '../../helpers/configuration';
import OrganizationAPI from '../../api/OrganizationAPI';


const schema = Joi.object().keys({
  swaggerName: Joi.string()
    .label('Model name')
    .min(2)
    .max(64)
    .required()
});

const schemaUploader = Joi.object().keys({
  fileSource: Joi.string()
    .min(2)
    .required(),
  fileName: Joi.string()
    .min(4)
    .required()
    .regex(/.*(\.mm|\.json)$/)
});

const BootstrapInput = withStyles(theme => ({
  root: {
    width: '100%',
    'label + &': {
      marginTop: theme.spacing(3)
    }
  },
  input: {
    borderRadius: 4,
    position: 'relative',
    backgroundColor: theme.palette.background.paper,
    border: '1px solid #ced4da',
    fontSize: 16,
    padding: '10px 26px 10px 12px',
    transition: theme.transitions.create(['border-color', 'box-shadow']),
    // Use the system font instead of the default Roboto font.
    fontFamily: [
      '-apple-system',
      'BlinkMacSystemFont',
      '"Segoe UI"',
      'Roboto',
      '"Helvetica Neue"',
      'Arial',
      'sans-serif',
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"'
    ].join(','),
    '&:focus': {
      borderRadius: 4,
      borderColor: '#80bdff',
      boxShadow: '0 0 0 0.2rem rgba(0,123,255,.25)'
    }
  }
}))(InputBase);

const { dashboardH2Titles } = globalStyles;
const styles = theme => ({
  dashboardH2Titles,
  lateralMenu: {
    padding: '1.5rem',
    backgroundColor: '#fff',
    height: '92%'
  },
  paddingTitle: {
    paddingBottom: '1rem'
  },
  root: {
    display: 'flex',
    flexWrap: 'wrap'
  },
  exampleBtn: {
    margin: 0,
    marginBottom: 14,
    width: '100%'
  },
  bootstrapFormLabel: {
    fontSize: 18
  },
  displayFullBtnText: {
    whiteSpace: 'normal'
  },
  selectEmpty: {},
  supportSection: {
    border: '1px solid rgb(234, 238, 251)',
    borderRadius: '4px',
    padding: '10px',
    height: 'auto',
    backgroundColor: '#fff',
    boxShadow: '0 2px 4px 0 rgba(0, 0, 0, 0.12), 0 0 6px 0 rgba(0, 0, 0, 0.04)',
    width: '270px',
    margin: 'auto',
    marginBottom: '5px',
    textAlign: 'center'
  },
  supportSectionTitle: {
    fontSize: '20px',
    color: '#324157'
  },
  subTitle: {
    margin: theme.spacing(1)
  },
  textSubTitle: {
    color: '#324157',
    fontSize: '20px'
  },
  inputSwaggerForm: {
    marginTop: '0',
    marginBottom: '15px',
    width: '-webkit-fill-available'
  }
});

class SwaggerForm extends Component {
  static contextType = UserContext;

  state = {
    invalidForm: true,
    invalidFormExample: true,
    swaggerName: '',
    selectedExample: {},
    selectedExampleName: '',
    invalidFormUploader: true,
    data: '',
    resources: [],
    paths: [],
    api: {},
    openResourceSelector: false,
    team: { id: null, name: '' },
    teamChoiceOpen: false
  };

  checkForm = () => {
    const data = {
      swaggerName: this.state.swaggerName
    };
    const result = schema.validate(data);
    this.setState({
      invalidForm: !!result.error
    });
  };

  handleChangeExample = event => {
    this.setState({
      selectedExampleName: event.target.value,
      selectedExample: JsonExamples.find(x => x.name === event.target.value),
      invalidFormExample: false
    });
  };

  handleClickExample = () => {
    this.setState({ data: this.state.selectedExample }, () =>
      this.handleCreateModel()
    );
  };

  handleChangeFromScratch = (type, value) => {
    this.setState({ [type]: value }, this.checkForm);
  };

  handleClickFromScratch = () => {
    if (this.state.invalidForm === false) {
      this.setState({ data: newTree(this.state.swaggerName) }, () =>
        this.handleCreateModel()
      );
    }
  };

  handleResourceSelectorClose = data => {
    this.setState({ openResourceSelector: false, data }, () => {
      if (data.name) {
        this.handleCreateModel();
      }
    });
  };

  handleTeamSelect = event => {
    this.setState({ team: event.target.value });
  };

  handleTeamChoiceClose = () => {
    this.setState({ teamChoiceOpen: false });
  };

  redirectToModelTree = (model) => {
    this.props.history.push({
      pathname: '/tree',
      state: {
        modelId: model.id,
        teamId: model.team,
        organizationId: model.organization
      }
    });
  }

  handleTeamChoiceClick = () => {
    ModelAPI.createModel(this.state.team.id, { data: this.state.data })
      .then(this.redirectToModelTree);
  };


  handleCreateModel = async () => {
    if (Configuration.GRAVITEE_ENABLED) {
      // Get the Gravitee organization
      const organizations = await OrganizationAPI.getMyOwnOrganizations();
      const graviteeOrganization = organizations.find((org) => org.name === 'Gravitee');

      // Create the API team
      const teamName = this.state.data.name;
      const capitalizedTeamName = teamName[0].toUpperCase()
        + teamName.substring(1, teamName.length);

      const teams = await TeamAPI.getMyTeams();
      let team = teams.find(t => t.team.name === capitalizedTeamName);
      if (!team) {
        team = await TeamAPI.createTeam(`${capitalizedTeamName} API`, graviteeOrganization.id);
      }

      // Create the model in the newly created team
      const model = await ModelAPI.createModel(team.id, { data: this.state.data })

      // Finally redirect to user to the tree view
      this.redirectToModelTree(model);
    } else {
      this.setState({ teamChoiceOpen: true });
    }
  }

  onDragOver = e => {
    e.preventDefault();
  };

  onDrop = e => {
    e.preventDefault();
    const files = e.dataTransfer.files;
    this.onFileChange(e, files[0]);
  };

  onFileChange = (e, file) => {
    const currFile = file || e.target.files[0];
    const reader = new FileReader();

    reader.onload = () => {
      const fileSource = reader.result;
      const fileName = currFile.name;

      const result = schemaUploader.validate({
        fileSource,
        fileName
      });

      if (!result.error) {
        const fileExtension = fileName.split('.').reverse()[0];
        if (fileExtension === 'mm') {
          FreemindAPI.uploadFreemind(fileSource).then(data => {
            this.setState({
              data,
              invalidFormUploader: false
            }, () => {
              this.handleCreateModel();
            })
          }).catch((error) => {
            Notification.error({
              title: 'Error',
              message:
                error.response.data.errorMessage ||
                'Could\'t import the .mm file.'
            });
          });
        } else if (fileExtension === 'json') {
          SwaggerAPI.getResourcesFromUploaded(fileSource)
            .then(data => {
              this.setState(
                {
                  resources: data.resources,
                  paths: data.paths,
                  api: data.api
                },
                () => {
                  this.setState({ openResourceSelector: true });
                }
              );
            })
            .catch(error => {
              Notification.error({
                title: 'Error',
                message:
                  error.response.data.message ||
                  'Could not find a valid schema in the endpoint'
              });
            });
        }
      } else {
        this.setState(
          {
            invalidFormUploader: true
          },
          () => {
            Notification.error({
              title: 'Error',
              message:
                'There was a problem uploading your file. Please make sure to upload the right extension (.mm, .json)'
            });
          }
        );
      }
    };

    reader.readAsText(currFile);
  };

  render() {
    const { classes } = this.props;
    const { teams } = this.context;
    const { team, teamChoiceOpen } = this.state;
    return (
      <>
        <div className={classes.lateralMenu}>
          <h2 className={`${classes.dashboardH2Titles} ${classes.paddingTitle}`}>New Model</h2>
          <div style={{ marginBottom: '24px' }}>
            <Typography align="left" className={classes.textSubTitle}>
              From an example...
            </Typography>
            <FormControl className={classes.exampleBtn}>
              <Select
                displayEmpty
                data-cy="dashboard-try-example-select"
                className={classes.selectEmpty}
                value={this.state.selectedExampleName}
                onChange={this.handleChangeExample}
                input={
                  <BootstrapInput
                    name="nameSelected"
                    id="age-customized-select"
                  />
                }
              >
                <MenuItem value="" disabled>
                  Select an example:
                </MenuItem>
                {JsonExamples.map((e, key) => {
                  return (
                    <MenuItem key={key} value={e.name}>
                      {e.name}
                    </MenuItem>
                  );
                })}
              </Select>
            </FormControl>
            <Button
              variant="contained"
              color="primary"
              data-cy="dashboard-try-example-btn"
              className={`el-button el-button--primary shared-width ${classes.displayFullBtnText} ${
                this.state.invalidFormExample ? 'is-disabled' : ''
              }`}
              disabled={this.state.invalidFormExample}
              type="button"
              onClick={this.handleClickExample}
            >
              Try the {this.state.selectedExampleName} example
            </Button>
          </div>

          <div style={{ marginBottom: '24px' }}>
            <Typography align="left" className={classes.textSubTitle}>
              ...or from scratch{' '}
            </Typography>
            <TextField
              id="outlined-name"
              label="New Model Name"
              size="small"
              className={classes.inputSwaggerForm}
              onChange={e => {
                this.handleChangeFromScratch('swaggerName', e.target.value);
              }}
              margin="normal"
              variant="outlined"
            />
            <Button
              variant="contained"
              color="primary"
              className={`el-button create-button el-button--primary shared-width ${
                this.state.invalidForm ? 'is-disabled' : ''
              }`}
              disabled={this.state.invalidForm}
              onClick={this.handleClickFromScratch}
              type="button"
            >
              Create empty model
            </Button>
          </div>

          <form onSubmit={() => this.handleCreateModel()}>
            <Typography align="left" className={classes.textSubTitle}>
              ...or from import{' '}
            </Typography>
            <div className="el-input">
              <div className="el-form-item">
                <label className="el-upload el-upload--text full-width">
                  <div
                    className="el-upload-dragger full-width"
                    onDragEnter={this.onDragEnter}
                    onDragLeave={this.onDragLeave}
                    onDragOver={this.onDragOver}
                    onDrop={this.onDrop}
                  >
                    <i className="el-icon-upload" />
                  </div>
                  <input
                    name="freemind"
                    className="el-upload__input"
                    accept="*.mm"
                    type="file"
                    onClick={event => {
                      event.target.value = null;
                    }}
                    onChange={this.onFileChange}
                    ref="input"
                  />
                </label>
                <div className="el-upload__tip">
                  Only Freemind and Swagger files are accepted (.mm, .json)
                </div>
              </div>
            </div>

            <div className="el-input double-ctn">
              <Button
                variant="contained"
                color="primary"
                className={`el-button import-button el-button--primary shared-width ${
                  this.state.invalidFormUploader ? 'is-disabled' : ''
                }`}
                disabled={this.state.invalidFormUploader}
                type="button"
                onClick={this.handleClickFromImport}
              >
                Import
              </Button>
            </div>
          </form>
        </div>
        <ResourceSelector
          open={this.state.openResourceSelector}
          close={this.handleResourceSelectorClose}
          resources={this.state.resources}
          paths={this.state.paths}
          api={this.state.api}
        />
        <TeamSelector
          team={team}
          teams={teams.map((t) => t.team)}
          teamChoiceOpen={teamChoiceOpen}
          handleTeamSelect={this.handleTeamSelect}
          handleTeamChoiceClose={this.handleTeamChoiceClose}
          handleTeamChoiceClick={this.handleTeamChoiceClick}
        />
      </>
    );
  }
}

export default withRouter(withStyles(styles)(SwaggerForm));
