import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
  Modal,
  Typography,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Button
} from '@material-ui/core';

const useStyles = makeStyles(() => ({
  modalContainer: {
    backgroundColor: '#e0e0e0',
    width: 'fit-content',
    position: 'absolute',
    top: '50%',
    left: '50%',
    padding: '20px',
    transform: 'translateY(-50%) translateX(-50%)',
    borderRadius: '10px',
    border: 'solid 1px black',
    textAlign: 'center'
  },
  formControl: {
    minWidth: '120px'
  },
  buttonSaveTeam: {
    margin: '10px'
  }
}));

export default function TeamSelector(props) {
  const classes = useStyles();

  return (
    <Modal
      open={props.teamChoiceOpen}
      onClose={() => props.handleTeamChoiceClose()}
    >
      <div className={classes.modalContainer}>
        <Typography>Where do you want to save it?</Typography>
        <FormControl className={classes.formControl}>
          <InputLabel id="select-label">Team</InputLabel>
          <Select
            data-cy="select-team-before-model-save"
            labelid="select-label"
            value={props.team}
            onChange={e => {
              props.handleTeamSelect(e);
            }}
          >
            {props.teams.map(t => {
              return (
                <MenuItem key={t.id} value={t}>
                  {t.name}
                </MenuItem>
              );
            })}
          </Select>
        </FormControl>
        <Button
          className={classes.buttonSaveTeam}
          variant="contained"
          onClick={() => props.handleTeamChoiceClick()}
        >
          Ok
        </Button>
      </div>
    </Modal>
  );
}
