import React, { useContext } from 'react';
import {
  Card,
  Container,
  DialogActions,
  DialogContent,
  TextField,
  Button
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import RadioButton from './CustomRadioButton';
import TextEditor from './TextEditor';
import RuleList from './RuleList';
import SnippetList from './SnippetList';

import { RuleContext } from '../../contexts/RuleContext';

const useStyles = makeStyles(() => ({
  container: {
    display: 'grid',
    gridTemplateColumns: '22% 56% 22%',
    gridColumnGap: '0.5rem',
    gridRowGap: '0.5rem',
    height: '100%',
    justifyItems: 'stretch'
  },
  mainContainerRows: {
    gridTemplateRows: '9.5rem 40% auto',
  },
  rulesListBlock: {
    gridColumn: 1,
    gridRow: '1 / 4'
  },
  editMetadataBlock: {
    gridColumn: 2,
    gridRow: 1,
    display: 'flex',
    paddingTop: '1.3rem'
  },
  yamlRuleBlock: {
    gridColumn: 2,
    gridRow: '2 / 4'
  },
  snippetsBlock: {
    gridColumn: 3,
    gridRow: '1 / 4',
    overflowY: 'auto'
  },
  saveAllBtn: {
    width: '-webkit-fill-available',
    gridColumn: '3 / 4',
    backgroundColor: '#55A646',
    '&:hover': {
      backgroundColor: '#448238'
    }
  },
  testAllBtn: {
    width: '-webkit-fill-available',
    color: '#5DBC42',
    '&:hover': {
      color: 'white',
      backgroundColor: '#5DBC42'
    }
  },
  operationsRadioBtn: {
    display: 'block',
    width: 'min-content',
    paddingTop: '0.5rem'
  },
  ruleNameInput: {
    gridRow: 1,
    margin: '10px',
    width: '-webkit-fill-available',
    height: '40px'
  },
  rulePathInput: {
    margin: '10px',
    width: '-webkit-fill-available'
  },
  operationInput: {
    margin: 'auto'
  },
  textfieldContainer: {
    width: '-webkit-fill-available'
  },
  rootInput: {
    borderRadius: '20px'
  },
  root: {
    height: '40px'
  },
  cssLabel: {
    fontSize: '15px',
    lineHeight: 0
  }
}));

export default function DialogRule(props) {
  const classes = useStyles();
  const { state } = useContext(RuleContext);
  const { currentRule, rules, isRulesSaved } = state;
  const {
    addRule,
    deleteRule,
    saveRules,
    testRules,
    onChange,
    onChangeTextEditor
  } = props;

  return (
    <>
      <DialogContent style={{ padding: '1rem 0 0.5rem 0' }}>
        <Container className={`${classes.container} ${classes.mainContainerRows}`}>
          <div className={classes.rulesListBlock}>
            <RuleList addRule={addRule} deleteRule={deleteRule} rules={rules} />
          </div>
          <Card className={classes.editMetadataBlock}>
            <div className={classes.operationsRadioBtn}>
              <RadioButton
                className={classes.operationInput}
                operation={currentRule.operation}
                onChange={onChange}
              />
            </div>
            <div className={classes.textfieldContainer}>
              <TextField
                className={classes.ruleNameInput}
                InputLabelProps={{
                  classes: {
                    root: classes.cssLabel
                  }
                }}
                InputProps={{
                  classes: {
                    notchedOutline: classes.rootInput,
                    root: classes.root
                  }
                }}
                value={currentRule.name}
                id="outlined-helperText"
                label="Rule name"
                name="name"
                margin="normal"
                variant="outlined"
                placeholder="Ex: Security rule on all POST"
                onChange={onChange}
              />
              <TextField
                className={classes.rulePathInput}
                InputLabelProps={{
                  classes: {
                    root: classes.cssLabel
                  }
                }}
                InputProps={{
                  classes: {
                    notchedOutline: classes.rootInput,
                    root: classes.root
                  }
                }}
                value={currentRule.path}
                id="outlined-helperText"
                label="Path"
                name="path"
                margin="normal"
                variant="outlined"
                placeholder="path.products.post.message"
                onChange={onChange}
              />
            </div>
          </Card>
          {currentRule.operation !== 'DROP' && (
            <>
              <div className={classes.yamlRuleBlock}>
                <TextEditor
                  yamlRule={currentRule.yamlRule}
                  onChangeTextEditor={onChangeTextEditor}
                />
              </div>
            </>
          )}
          <Card className={classes.snippetsBlock}>
            <SnippetList />
          </Card>
        </Container>
      </DialogContent>
      <DialogActions>
        <Container className={classes.container}>
          <Button
            variant="outlined"
            className={classes.testAllBtn}
            onClick={testRules}
          >
            Test all rules
          </Button>
          <Button
            disabled={isRulesSaved}
            variant="contained"
            color="primary"
            className={classes.saveAllBtn}
            onClick={saveRules}
          >
            Save rules
          </Button>
        </Container>
      </DialogActions>
    </>
  );
}
