import BaseAPI from './BaseAPI';

export default class ModelAPI extends BaseAPI {
  static getModelNames(teamId) {
    const params = teamId ? { params: { teamId } } : {};
    return this.execute('get', 'users/me/modelsList', params);
  }

  static getModels(params) {
    return this.execute('get', `users/me/models`, { params }, true);
  }

  static getModelSwaggerAsFile(modelId, fileType) {
    return this.execute('get', `models/${modelId}/swagger?type=${fileType}`);
  }

  static createModel(teamId, model) {
    return this.execute('post', `/teams/${teamId}/models`, {
      data: model
    });
  }

  static getModel(modelId) {
    return this.execute('get', `/models/${modelId}`);
  }

  static saveModel(modelId, model, hasExited) {
    if (hasExited) {
      return this.execute('put', `/models/${modelId}?hasExited=true`, { data: model });
    }
    return this.execute('put', `/models/${modelId}`, { data: model });
  }

  static freezeModel(modelId) {
    return this.execute('post', `/models/${modelId}?action=freeze`);
  }

  static revertModel(modelId) {
    return this.execute('post', `/models/${modelId}?action=revert`);
  }

  static duplicateModel(modelId) {
    return this.execute('post', `/models/${modelId}?action=duplicate`);
  }

  static deleteModel(modelId) {
    return this.execute('delete', `/models/${modelId}`);
  }

  static renameModel(modelId, name) {
    return this.execute('patch', `/models/${modelId}`, { data: { name } });
  }

  static publishToApiManager(modelId) {
    return this.execute('post', `/models/${modelId}/gravitee`);
  }

  static changeOrganizationModel(modelId, tags, name, organizationId) {
    return this.execute('patch', `/models/${modelId}`, {
      data: { tags, name, organizationId }
    });
  }

  static getModelRules(modelId) {
    return this.execute('get', `/models/${modelId}/refinerRules`);
  }

  static testModelRule(modelId, rules) {
    return this.execute(
      'post',
      `/models/${modelId}/refinerRules?testOnly=true`,
      {
        data: rules
      }
    );
  }

  static createModelRule(modelId, rules) {
    return this.execute('put', `/models/${modelId}/refinerRules`, {
      data: rules
    });
  }

  static updateModelRule(modelId, ruleId, rule) {
    return this.execute('put', `/models/${modelId}/refinerRules/${ruleId}`, {
      data: rule
    });
  }

  static deleteModelRule(modelId, ruleId) {
    return this.execute('delete', `/models/${modelId}/refinerRules/${ruleId}`);
  }
}
