import React from 'react';
import AceEditor from 'react-ace';

import 'ace-builds/src-noconflict/mode-yaml';
import 'ace-builds/src-noconflict/theme-monokai';

const Editor = props => {
  const { yamlRule, onChangeTextEditor } = props;
  return (
    <AceEditor
      fontSize={16}
      mode="yaml"
      theme="monokai"
      style={{
        width: '-webkit-fill-available',
        height: '100%',
        minHeight: '100%'
      }}
      value={yamlRule}
      onChange={text => onChangeTextEditor(text)}
      name="yamlRule"
      editorProps={{ $blockScrolling: false }}
      showPrintMargin
      showGutter
      highlightActiveLine
      setOptions={{
        showLineNumbers: true,
        tabSize: 2
      }}
    />
  );
};

export default Editor;
